import React from "react";

import "./about.css";
import serviceImage from "../../static/images/background-1.png";

import Carousel from "../carousel/carousel";

function About() {
  return (
    <div className="about-container">
      <section style={{ paddingTop: "10vh" }}>
        <p className="about--title">WHAT IS DTS?</p>
        <div className="about--wrapper">
          <div className="about--card--container">
            <CarouselBuilder />
          </div>
          <div className="space-for-two-container">
            s<div className="space-for-container"></div>
            <div className="space-for-container"></div>
          </div>
        </div>
      </section>
      <section id="services">
        <p className="services--title">OUR SERVICES</p>
        <div className="services--wrapper">
          <div className="services--img">
            <img src={serviceImage} alt="Our Services Img" />
          </div>
          <div className="services-list--wrapper">
            <div className="services--list-container">
              {servicesList.map((service) => (
                <ServiceItem key={service.number} item={service} />
              ))}
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

function CarouselBuilder() {
  return (
    <Carousel>
      <p>
        Digital Tutory Services (DTS) is a leading distance learning, M-learning
        platform facilitating Academic and Professional courses across boarders
        for tutors and students.
      </p>
      <p>
        Digital Tutory Services (DTS) is a leading distance learning, M-learning
        platform facilitating Academic and Professional courses across boarders
        for tutors and students.
      </p>
      <p>
        Digital Tutory Services (DTS) is a leading distance learning, M-learning
        platform facilitating Academic and Professional courses across boarders
        for tutors and students.
      </p>
    </Carousel>
  );
}

function ServiceItem(props) {
  return (
    <div className="service-item">
      <h1 className="service-item-number">{props.item.number}</h1>
      <p className="service-item-title">{props.item.title}</p>
    </div>
  );
}

const servicesList = [
  { number: "01", title: "HYBRID TO TELEPORT" },
  { number: "02", title: "TELEPORT CENTERS" },
  { number: "03", title: "ONLINE LEARNING" },
  { number: "04", title: "TUTOR PRODUCTIONS" },
  { number: "05", title: "VIDEO ON DEMAND" },
  { number: "06", title: "STUDENT SCHOLARSHIP" },
  { number: "07", title: "EASY PAYMENT METHODS" },
  { number: "08", title: "HYBRID DEBIT CARD" },
  { number: "09", title: "STUDENT COORDINATION" },
  { number: "10", title: "INSTITUTIONAL PLATFORMS" },
];

export default About;
