import React, { useEffect } from "react";
import "./App.css";

import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import Home from "./components/home/home";
import Tutors from "./components/tutors/tutors";
import NavBar from "./components/navBar/navBar";
import About from "./components/about/about";
import TutorsList from "./components/tutorsList/tutorsList";
import TutorProfile from "./components/tutorProfile/tutorProfile";
import Contact from "./components/contact/contact";
import WelcomeSlider from "./components/welcomeSlider/welcomeSlider";

import { TweenMax, Power3 } from "gsap";

function App() {
  useEffect(() => {
    TweenMax.to(".slider", {
      duration: 1.2,
      delay: 0.8,
      transform: "translateY(-100%)",
      ease: Power3.easeIn,
    });
  }, []);

  return (
    <div className="app-body">
      <div className="background-img"></div>
      <div className="home slider">
        <WelcomeSlider />
      </div>
      <Router>
        <NavBar />

        <Switch>
          <Route path="/" exact component={Home}></Route>

          <Route path="/about" exact component={About}></Route>
          <Route path="/tutors" exact component={Tutors}></Route>
          <Route path="/tutors/:type" exact component={TutorsList}></Route>
          <Route path="/tutor/:id" exact component={TutorProfile}></Route>
          <Route path="/contact" exact component={Contact}></Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
