import React, { useRef, useEffect } from "react";
import { useHistory } from "react-router-dom";
import "./tutorsList.css";

import tutorImg from "../../static/images/background-1.png";
import { LazyLoadImage } from "react-lazy-load-image-component";

function Tutor() {
  const history = useHistory();

  const handleTutorClick = (id) => {
    history.push(`/tutor/${id}`);
  };

  return (
    <section className="tutorslist--wrapper">
      <div
        style={{
          height: "10vh",
          width: "100%",
        }}
      ></div>
      <div className="title-section">
        <p className="title">
          <span className="bigger">DTS</span> PANEL OF TUTORS{" "}
        </p>
        <div className="filter-teacher">Filter teachers here</div>
      </div>

      <div className="tutors--list">
        <TutorTile
          item={{
            id: "1",
            name: "Manoj Solangarachchi",
            imgPath: "https://primetuition.lk/functions/uploads/1588705094.jpg",
            handleClick: handleTutorClick,
          }}
        />
        <TutorTile
          item={{
            id: "2",
            name: "Manoj Solangarachchi",
            imgPath: "https://primetuition.lk/functions/uploads/1588705094.jpg",
            handleClick: handleTutorClick,
          }}
        />
        <TutorTile
          item={{
            id: "3",
            name: "Manoj Solangarachchi",
            imgPath: "https://primetuition.lk/functions/uploads/1588705094.jpg",
            handleClick: handleTutorClick,
          }}
        />
        <TutorTile
          item={{
            id: "4",
            name: "Manoj Solangarachchi",
            imgPath: "https://primetuition.lk/functions/uploads/1588705094.jpg",
            handleClick: handleTutorClick,
          }}
        />
        <TutorTile
          item={{
            id: "5",
            name: "Manoj Solangarachchi",
            imgPath: "https://primetuition.lk/functions/uploads/1588705094.jpg",
            handleClick: handleTutorClick,
          }}
        />
        <TutorTile
          item={{
            id: "6",
            name: "Manoj Solangarachchi",
            imgPath: "https://primetuition.lk/functions/uploads/1588705094.jpg",
            handleClick: handleTutorClick,
          }}
        />
      </div>
    </section>
  );
}

function TutorTile(props) {
  const { imgPath, name, id, handleClick } = props.item;

  return (
    <div className="tutor-tile-container" onClick={() => handleClick(id)}>
      <LazyLoadImage src={imgPath} alt="Hey guys" />
      <p>{name}</p>
    </div>
  );
}

export default Tutor;
