import React, { useRef, useEffect } from "react";
import "./tutors.css";
import { useHistory } from "react-router-dom";
import imagePath from "../../static/images/background-1.png";

import { TweenMax, Power3 } from "gsap";

function Tutors() {
  const history = useHistory();
  useEffect(() => {
    TweenMax.from(".hexagon", {
      duration: 0.3,
      transform: "translate(0)",
      stagger: 0.2,
      ease: Power3.easeIn,
    });
  }, []);

  const handleHexagonClick = (path) => {
    history.push(`/tutors/${path}`);
  };

  const hexagonData = [
    { name: "hex0" },
    {
      name: "hex1",
      title: "LEADERSHIP & PUBLIC SPEAKING",
      path: "leadership",
      onClick: handleHexagonClick,
    },
    {
      name: "hex2",
      title: "NATIONAL A\\Ls & O\\Ls",
      path: "national",
      onClick: handleHexagonClick,
    },
    { name: "hex3", title: "SAT", path: "sat", onClick: handleHexagonClick },
    {
      name: "hex4",
      title: "IELTS",
      path: "ielts",
      onClick: handleHexagonClick,
    },
    {
      name: "hex5",
      title: "FOREIGN LANGUAGES",
      path: "languages",
      onClick: handleHexagonClick,
    },
    {
      name: "hex6",
      title: "BUSINESS & SPOKEN ENGLISH",
      path: "business",
      onClick: handleHexagonClick,
    },
  ];
  return (
    <section className="tutors--wrapper">
      <div className="hexagon-container">
        {hexagonData.map((hexagon) => {
          return <HexagonButton key={hexagon.name} item={hexagon} />;
        })}
      </div>
      <div className="search--container">
        <SearchTutors />
      </div>
    </section>
  );
}

function SearchTutors() {
  return (
    <form action="">
      <input type="text" placeholder="Will build the AWS api is available" />
      <select name="teacher_type" id="">
        <option value="prime">PrimeTuition.lk</option>
        <option value="primeG">PrimeTuitition.org</option>
        <option value="hybrid">Hybrid to Teleport</option>
      </select>
    </form>
  );
}

function HexagonButton(props) {
  return (
    <div className={` hexagon ${props.item.name}`}>
      <div
        className="wrapper"
        onClick={(e) => {
          props.item.onClick(props.item.path);
        }}
      >
        <img src={imagePath} alt="" />
        <p>{props.item.title}</p>
      </div>
    </div>
  );
}

export default Tutors;
