import React, { useState } from "react";
import "./navBar.css";
import { useHistory, useLocation } from "react-router-dom";
function NavBar() {
  const history = useHistory();

  //get the current path and define the variable whether path is at home
  const location = useLocation();
  const isHome = location.pathname === "/";

  const [selectedPath, setSelectedPath] = useState(location.pathname);

  const handleClick = (path) => {
    history.push(path);
    setSelectedPath(path);
  };

  const links = [
    { title: "What is DTS?", path: "/about" },
    { title: "DTS Tutors", path: "/tutors" },
    { title: "News Feed", path: "/news" },
    { title: "Partners", path: "/partners" },
    // { title: "Libary", path: "/libary" },
    { title: "Contact Us", path: "/contact" },
  ];

  return (
    <nav className={`nav-bar ${isHome && "home"}`}>
      <div className="nav-bar--bg"></div>
      <ul className={`nav-links ${isHome && "home"}`}>
        <HomeButton
          data={{
            handleClick: handleClick,
            isHome: isHome,
          }}
        />
        {links.map((link) => (
          <NavLink
            key={link.path}
            props={{
              title: link.title,
              path: link.path,
              handleClick: handleClick,
              selectedTitle: selectedPath,
            }}
          ></NavLink>
        ))}
      </ul>
    </nav>
  );
}

function NavLink(props) {
  const { title, path, handleClick, selectedTitle } = props.props;
  const isSelected = path === selectedTitle;
  return (
    <div>
      <li
        className={`nav-link ${isSelected && "link-active"}`}
        onClick={(e) => {
          console.log(e.target);
          handleClick(path);
        }}
      >
        {title}
      </li>
      <div className={`bottom_bar ${isSelected && "link-active"}`}></div>
    </div>
  );
}

function HomeButton(props) {
  const { isHome, handleClick } = props.data;
  return (
    <div
      className="nav-bar--home"
      onClick={() => handleClick(isHome ? "/map" : "/")}
    >
      <p>{isHome ? "Map" : "Home"}</p>
    </div>
  );
}

export default NavBar;
