import React from "react";

import "./welcomeSlider.css";
import dtsLogo from "../../static/images/dts-logo.png";
import background from "../../static/images/background-1.png";

function WelcomeSlider() {
  return (
    <div className="welcome--slider">
      <div className="center">
        <img src={dtsLogo} className="logo" />
        <h1>DIGITAL TUTORY SERVICES</h1>
      </div>
      <div className="bottom">
        <p>CONNECTING THE DOTS</p>
        <div className="circles">
          <div className="circle highlight"></div>
          <div className="circle"></div>
          <div className="circle"></div>
          <div className="circle"></div>
          <div className="circle"></div>
          <div className="circle highlight"></div>
        </div>
      </div>
    </div>
  );
}

export default WelcomeSlider;
