import React, { useEffect } from "react";
import "./tutorProfile.css";
function TutorProfile() {
  const hexagonData = [
    {
      name: "hex0",
      imgPath: "https://primetuition.lk/functions/uploads/1588705094.jpg",
    },
    {
      name: "hex1",
      imgPath: "https://primetuition.lk/functions/uploads/1588705094.jpg",
      title: "TEACHING SPECIALIST",
    },
    {
      name: "hex2",
      imgPath: "https://primetuition.lk/functions/uploads/1588705094.jpg",
      title: "TEACHING SPECIALIST",
    },
    {
      name: "hex3",
      imgPath: "https://primetuition.lk/functions/uploads/1588705094.jpg",
      title: "TEACHING SPECIALIST",
    },
    {
      name: "hex4",
      imgPath: "https://primetuition.lk/functions/uploads/1588705094.jpg",
      title: "TEACHING SPECIALIST",
    },
    {
      name: "hex5",
      imgPath: "https://primetuition.lk/functions/uploads/1588705094.jpg",
      title: "TEACHING SPECIALIST",
    },
    {
      name: "hex6",
      imgPath: "https://primetuition.lk/functions/uploads/1588705094.jpg",
      title: "TEACHING SPECIALIST",
    },
  ];

  return (
    <div className="tutor-profile">
      <section className="tutor-profile--main">
        <div className="profile--image-container">
          {hexagonData.map((hexagon) => (
            <Hexagon key={hexagon.name} item={hexagon} />
          ))}
        </div>
        <div className="profile--description">
          <TutorDetails />
        </div>
      </section>
      <section className="tutor-profile--secondary">
        <div className="about-tutor--video">
          <iframe
            src="https://www.youtube.com/embed/qh6-UB7SAtM"
            frameborder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowfullscreen
          ></iframe>
        </div>
        <div className="timetable--container"></div>
      </section>
    </div>
  );
}

function TutorDetails(props) {
  return (
    <div className="profile--description--wrapper">
      <div className="fill-gap"></div>
      <p className="card bigger">Thamira Herath BS.c</p>
      <div className="fill-gap"></div>
      <p className="card">Advanced Level Chemistry</p>
      <p className="card">Sinhala Medium</p>
      <p className="card">Over 15 years of teaching</p>
      <p className="card">More than 2000 students</p>
      <div className="fill-gap"></div>
      <div className="fill-gap"></div>
    </div>
  );
}

function Hexagon(props) {
  const { name, imgPath, title } = props.item;

  return (
    <div className={`hexagon ${name}`}>
      <div className="wrapper">
        {imgPath && <img src={imgPath} className={`${title && "darken"}`} />}
        {title && <p>{title}</p>}
      </div>
    </div>
  );
}

export default TutorProfile;
